.App {
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-rows: 8% 1fr 1fr 1fr 8%;
    grid-template-columns: 15% 1fr 1fr 1fr 1fr;
}

header {
    grid-row: 1/span 1;
    grid-column: 1/span 5;
    display: flex;
    justify-content: space-between;
    background-color: orange;
}

header>h1 {
    margin-left: 10px;
    margin-top: 10px;
}

#barra-busca {
    width: 200px;
    height: 20px;
    margin-right: 10px;
    margin-top: 10px;
}

footer{
    grid-row: 5/span 1;
    grid-column: 1/span 5;
    background-color: gray;
    color: white;
}

footer>h4 {
    margin-left: 10px;
}

nav {
    grid-row: 2/span 3;
    grid-column: 1/span 1;
    border-right: 2px solid grey;
}

ul {
    list-style-type: none;
    margin-left: 10px;
    margin-right: 0;
    padding: 0;
}

ul>li:hover {
    background-color: orange;
    color: white;
    cursor: pointer;
}

div.video>img {
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
}

article {
    margin: 10px;
    border: 1px solid green;
    cursor: pointer;
}

.video:hover {
    opacity: 0.5;
}
  

.container {
    padding: 2px 16px;
}

#video-1 {
    grid-row: 2/span 1;
    grid-column: 2/span 1;
}